.legend_div {
	padding: 4% 8%;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 2%;
}

.poster_div {
	width: 100%;
	height: 30vh;
	padding-top: 10%;

	background-image: url('../../assets/images/backgrounds/bangalore.jpg');
	background-size: cover;
}

/* ======== styling the navigation biv and buttons ============ */
.navigate_div {
	width: 100%;
	/* margin: 50% 0; */
	display: flex;
	justify-content: space-between;
}

.navigate_button {
	width: 10%;
	background-color: var(--black);
	font-size: 2rem;
	color: var(--milk_white);
	padding: 1% 4%;
}

/* ========================================================== */

.legend_div_info h4 {
	font-size: 1.3rem;
	font-weight: lighter;
	color: var(--gray);
}

.legend_div_info h1 {
	font-size: 3rem;
	font-weight: 700;
	letter-spacing: 1px;
}

.legend_div_info h3 {
	font-size: 1.8rem;
	font-weight: 500;
	color: #474747;
}

.legend_div_info p {
	font-size: 1.4rem;
	font-weight: 500;
	line-height: 2.3rem;
	color: #474747;
	/* letter-spacing: 1px; */
}

.table_form_info_div_child {
	width: 100%;
	display: flex;
	padding: 1.4% 0;

	border-bottom: 1px solid var(--footer_white);
}

.table_form_info_div_child h2 {
	font-size: 1.4rem;
	font-weight: 500;
	margin: 0;
	width: 50%;
}

.table_form_info_div_child h3 {
	font-size: 1.4rem;
	margin: 0;
	width: 50%;
}

.ability_div {
	background-color: var(--black);
	width: 100%;
	height: auto;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	padding: 8% 0;
}

.ability_div_child {
	text-align: center;
}

.ability_div_child img {
	width: 80%;
	height: auto;
}

.ability_div_child h2 {
	color: red;
	font-size: 1rem;
	margin: 0;
}

.ability_div_child h3 {
	color: var(--footer_white);
	font-size: 2rem;
	font-weight: 500;
	margin: 0;
}

/* ================ for the mobile view ==================== */

@media (min-width: 320px) and (max-width: 425px) {
	.legend_div {
		display: block;
	}

	/* ====== changing the image div height ===============*/
	.legend_div_img {
		/* width: 100%;
		height: auto; */
		height: 60vh;
	}
	/* ========= changing the ability div ============ */
	.ability_div {
		display: block;
	}

	.ability_div_child {
		margin: 15% 0;
	}

	.table_form_info_div {
		margin: 5% 0;
	}

	/* ====== changing the info table ================== */

	.table_form_info_div_child {
		padding: 3% 0;
	}

	/* ========== changing the info of the legends========== */
	.legend_div_info h4 {
		font-size: 1.1rem;
	}

	.legend_div_info h1 {
		font-size: 2.5rem;
	}

	.legend_div_info h3 {
		font-size: 1.3rem;
	}

	.legend_div_info p {
		font-size: 1.2rem;
		line-height: 2rem;
	}
}
